import React from "react";
import styled from "styled-components";
import "./Card.scss";
import { redirectTo } from "../../util/redirect.js";

const cardWidth = 30;

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
`;

const HeadingBox = styled.div`
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.5);
  height: 25em;
  width: ${cardWidth}em;
  justify-content: center;
  align-items: center;
  padding: 2em;
  text-align: justify;
  color: rgb(72, 38, 115, 1);
  overflow: hidden;
`;

const NameSubBox = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.5);

  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  margin-bottom: 2px;
  height: 5em;
  width: ${cardWidth}em;
  color: rgb(72, 38, 115, 1);
`;

const TechSubBox = styled.div`
  display: flex;
  flex-direction: row;
  background-color: rgba(255, 255, 255, 0.5);
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  margin-top: 2px;
  height: 5em;
  color: rgb(72, 38, 115, 1);
  width: ${cardWidth}em;
  padding: 1%;
`;

const PreviewImage = styled.img`
  margin-top: 0;
  border-radius: 10px;
  height: 11em;
  width: 15em;
`;

const Icon = styled.div`
  height: 3em;
  width: 3em;
  color: red;
`;

const Card = ({ name, description, url, image, tech }) => {
  return (
    <>
      <Wrapper
        className="card"
        onClick={() => {
          redirectTo(url);
        }}
      >
        <NameSubBox>
          <h1>{name}</h1>
        </NameSubBox>
        <HeadingBox>
          <PreviewImage src={image} />
          <p>{description}</p>
        </HeadingBox>
        <TechSubBox>
          {tech.map((Icon, index) => (
            <Icon />
          ))}
        </TechSubBox>
      </Wrapper>
    </>
  );
};

export default Card;
